function hexToRgb(hex: string) {
  const current = hex.replace(/^#/, '');
  const bigint = parseInt(current, 16);

  return [(bigint >> 16) & 255, (bigint >> 8) & 255, bigint & 255];
}

export function lightenColor(color: string, factor: string) {
  try {
    let normalizedColor;

    if (typeof color === 'string') {
      normalizedColor = hexToRgb(color);
    } else if (Array.isArray(color) && color.length === 3) {
      normalizedColor = color;
    } else if (Array.isArray(color) && color.length === 4) {
      normalizedColor = color.slice(0, 3);
    }

    if (!normalizedColor) {
      return color;
    }

    return `rgba(${normalizedColor.join(',')}, ${factor})`;
  } catch (e) {
    return color;
  }
}
