import React from 'react';
import { clientManager } from '@talkdeskchat-sdk/dce-sdk';
import { lightenColor } from '../../utils/colorTransform';
import styles from './ScreenLoading.module.css';

function ScreenLoading(props: { loading: boolean }) {
  const { loading } = props;
  const backgroundColor = clientManager.sdk?.theming?.chatThemeColor || '#1e0044';
  const enableConnectionFromTriggerButton =
    clientManager.sdk?.selfHostedApp?.enableConnectionFromTriggerButton || false;

  return (
    <div className={styles.screenLoadingWrapper} style={{ display: loading ? 'block' : 'none' }}>
      {enableConnectionFromTriggerButton && (
        <div className={styles.ldsEllipsis}>
          <div style={{ backgroundColor: lightenColor(backgroundColor, '0.8') }} />
          <div style={{ backgroundColor: lightenColor(backgroundColor, '0.7') }} />
          <div style={{ backgroundColor: lightenColor(backgroundColor, '1.0') }} />
          <div style={{ backgroundColor: lightenColor(backgroundColor, '0.9') }} />
        </div>
      )}
    </div>
  );
}

export default ScreenLoading;
