import React, { useEffect, useRef } from 'react';
import i18next from 'i18next';
import bus from '@talkdeskchat-sdk/common/utils/event-bus';

export interface Payload {
  id: string;
  messages: string[];
}

const VoiceoverProvider = ({ children }: React.PropsWithChildren) => {
  const boxRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const event = 'voiceover';
    const callback = (payload: Payload) => {
      if (boxRef.current) {
        const messages = (payload?.messages || [])
          .map((message) => i18next.t(message || ''))
          .join('\n');

        boxRef.current.innerText = messages;
      }
    };

    bus.on(event, callback);

    return () => {
      bus.off(event, callback);
    };
  }, []);

  return (
    <>
      {children}
      <div
        style={{
          position: 'absolute',
          left: 0,
          top: 0,
          width: 1,
          height: 1,
          overflow: 'hidden',
          border: 0,
          clip: 'rect(0 0 0 0)',
          margin: -1,
          padding: 0,
          userSelect: 'none',
        }}
      >
        <div ref={boxRef} role="log" aria-atomic="true" aria-live="polite" />
      </div>
    </>
  );
};

export default VoiceoverProvider;
