/* eslint-disable no-unused-expressions */
import React from 'react';
import { render } from 'react-dom';
import { ReactBot } from '@botonic/react/src/react-bot';
import { WebchatApp } from '@botonic/react/src/webchat-app';
import { Webchat } from '@botonic/react/src/webchat';
import Logger from '@talkdeskchat-sdk/common/utils/logger';
import VoiceoverProvider from '@talkdeskchat-sdk/channel-components/components/VoiceoverProvider';
import {
  connectToTheConversation,
  handleLivechatMessages,
  conversationExists,
  addStoreEventListenerLivechat,
  initiateLivechatImmediately,
  endChatHandler,
  addChatWelcomeMessages,
  context,
  typingProviderInstance,
  clientManager,
  ClientProvider,
  startConnectionFromTriggerButton,
} from '@talkdeskchat-sdk/dce-sdk';
import './SelfHostedApp.css';
import { getTextAreaElement } from '@talkdeskchat-sdk/dce-sdk/src/helpers';
import { DCEPayloads } from '@talkdeskchat-sdk/common/enums';
import { InitialScreen } from '@talkdeskchat-sdk/channel-components/components/InitialScreen/InitialScreen';
import { ErrorBoundary } from 'react-error-boundary';
import { StyleSheetManager } from 'styled-components';
import { interactionIdExists } from '@talkdeskchat-sdk/dce-sdk/src/storageLivechat';
import ScreenLoading from '@talkdeskchat-sdk/channel-components/components/ScreenLoading';
import { WebchatWrapper } from './WebchatWrapper';

export default class SelfHostedApp extends WebchatApp {
  constructor({
    theme = {},
    persistentMenu,
    blockInputs,
    enableEmojiPicker,
    enableValidation,
    enableAttachments,
    onInit,
    onOpen,
    onClose,
    onMessage,
    storageKey,
    enableUserInput,
    livechat,
    appId,
    notifications,
    touchPointConfig,
    region,
    shadowDOM,
    onConversationStartWebchat,
    sdkWelcomeMessages,
    enableConnectionFromTriggerButton,
    ...botOptions
  }) {
    super({
      theme,
      persistentMenu,
      blockInputs,
      enableEmojiPicker,
      enableValidation,
      enableAttachments,
      onInit,
      onOpen,
      onClose,
      onMessage,
      storageKey,
      enableUserInput,
      shadowDOM,
    });
    this.notifications = notifications;
    this.enableUserInput = enableUserInput;
    this.livechat = livechat;
    this.appId = appId;
    this.touchPointConfig = touchPointConfig;
    this.region = region;
    this.onConversationStartWebchat = onConversationStartWebchat;
    this.enableAttachments = enableAttachments;
    this.enableValidation = enableValidation;
    this.sdkWelcomeMessages = sdkWelcomeMessages;
    this.enableConnectionFromTriggerButton = enableConnectionFromTriggerButton;

    // This allows the widget to move accordingly with the presence of a keyboard
    /* eslint-disable no-undef */
    document.addEventListener('DOMContentLoaded', () => {
      if (window.visualViewport) {
        window.visualViewport.addEventListener('resize', () => {
          let botonic =
            document.getElementById('botonic-webchat') ??
            document.getElementById('tdWebchat')?.shadowRoot?.getElementById('botonic-webchat');

          let keyboardOffetsetValue =
            window.innerHeight - window.visualViewport.height - window.visualViewport.offsetTop;

          if (window.visualViewport.pageTop > window.innerHeight) keyboardOffetsetValue = 0;
          if (keyboardOffetsetValue >= 340 && window.visualViewport.offsetTop === 0)
            keyboardOffetsetValue = 36;
          if (botonic) {
            botonic.style.bottom = `${keyboardOffetsetValue + 20}px`;
          }
        });
      }
    });
    /* eslint-enable no-undef */

    if (!this.livechat) {
      this.bot = new ReactBot({
        ...botOptions,
      });
    } else {
      clientManager.reset(ClientProvider.TWILIO);
      const runConnectToTheConversation = () => {
        connectToTheConversation()
          .then()
          .catch(() => {
            endChatHandler(this);
          })
          .finally(() => {
            this.setShowUserInputContainerLockMask(false);
            this.setScreenLoading(false);
          });
      };

      addStoreEventListenerLivechat(this, runConnectToTheConversation).then(() => {
        if (conversationExists()) {
          runConnectToTheConversation();
        } else {
          this.onOpenInitialScreen();
        }
      });
    }
  }

  render(dest) {
    const host = this.shadowDOM ? dest.attachShadow({ mode: 'open' }) : dest;

    render(
      <VoiceoverProvider>
        <React.Suspense fallback={<></>}>
          <ErrorBoundary
            onError={(error) => {
              Logger.logError(new Error(error));
            }}
          >
            <StyleSheetManager target={host}>
              <WebchatWrapper
                className={this.livechat ? 'digital-webchat-wrapper' : 'ai-webchat-wrapper'}
                style={{
                  botAnchorTextColor: this.theme.message.bot.style.anchorTextColor,
                  botAnchorTextColorVisited: this.theme.message.bot.style.anchorTextColorActive,
                  userAnchorTextColor: this.theme.message.user.style.anchorTextColor,
                  userAnchorTextColorVisited: this.theme.message.user.style.anchorTextColorActive,
                  anchorDecoration: 'underline',
                }}
              >
                <Webchat
                  ref={this.webchatRef}
                  theme={{ ...this.theme }}
                  persistentMenu={this.persistentMenu}
                  enableEmojiPicker={this.enableEmojiPicker}
                  enableAttachments={this.enableAttachments}
                  enableUserInput={this.enableUserInput}
                  storageKey={this.storageKey}
                  // eslint-disable-next-line no-undef
                  storage={this.livechat ? null : localStorage}
                  getString={(stringId, session) => this.bot.getString(stringId, session)}
                  setLocale={(locale, session) => this.bot.setLocale(locale, session)}
                  onInit={(...args) => this.onInitWebchat(...args)}
                  onOpen={(...args) => this.onOpenWebchat(...args)}
                  onClose={(...args) => this.onCloseWebchat(...args)}
                  onUserInput={(...args) => this.onUserInput(...args)}
                  className="tdWebchat"
                  initialDevSettings={{ keepSessionOnReload: true, showSessionView: false }}
                  host={this.shadowDOM ? host : undefined}
                  shadowDOM={this.shadowDOM}
                  customOnChange={(value) => {
                    if (value?.length > 0) {
                      typingProviderInstance.typing();
                    }
                  }}
                  CustomScreenLoading={ScreenLoading}
                />
              </WebchatWrapper>
            </StyleSheetManager>
          </ErrorBoundary>
        </React.Suspense>
      </VoiceoverProvider>,
      this.getReactMountNode(host),
    );
  }

  setUserInput(isEnabled) {
    this.updateWebchatSettings({ enableUserInput: isEnabled });
  }

  onOpenInitialScreen() {
    if (this.touchPointConfig?.initialScreen?.showInitialScreen) {
      const { controls } = this.touchPointConfig.initialScreen;

      const formControls = controls.filter(
        (control) => control.type === 'field' || control.type === 'dropdown',
      );
      const defaultValues = formControls.reduce((acc, control) => {
        acc[control.argument] = context.initialScreenContext?.[control.argument] || undefined;

        return acc;
      }, {});

      const isAllFieldAreFilled =
        formControls.length > 0 &&
        Object.values(defaultValues).every((value) => value !== undefined);

      // if all fields are filled, then we don't show the initial screen
      if (isAllFieldAreFilled) {
        addChatWelcomeMessages(this);
        startConnectionFromTriggerButton();

        return;
      }

      this.webchatRef.current?.updateWebchatSettings?.({
        theme: {
          ...this.webchatRef.theme,
          coverComponent: {
            component: InitialScreen,
            props: {
              initialScreenConfig: this.touchPointConfig || {},
              appId: this.appId,
              enableValidation: this.enableValidation,
              onSubmitInitialScreen: (args, enableStartConversation) => {
                addChatWelcomeMessages(this);
                context.setInitialScreenContext(args);
                if (enableStartConversation) {
                  initiateLivechatImmediately();
                }
              },
              theme: this.theme,
              defaultValues,
            },
          },
        },
      });

      this.webchatRef.current?.openCoverComponent?.();
    } else {
      addChatWelcomeMessages(this);
    }
  }

  onInitWebchat(args) {
    super.onInitWebchat(args);
    this.updateBotonicTextInputReference();
  }

  onOpenWebchat(args) {
    super.onOpenWebchat(args);
    startConnectionFromTriggerButton();
  }

  // eslint-disable-next-line class-methods-use-this
  updateBotonicTextInputReference() {
    const botonicTextAreaReference = getTextAreaElement();

    if (!botonicTextAreaReference) {
      return;
    }

    // we spy on the value setter, because inside the botonic
    // library, it directly sets the value when a new emoji
    // is choosen via the emoji-picker-react, and this
    // does not trigger any change/keypress/keyup or other events
    Object.defineProperty(botonicTextAreaReference, 'value', {
      set(newValue) {
        if (newValue.length > this.maxLength) {
          return;
        }

        // eslint-disable-next-line no-undef
        Object.getOwnPropertyDescriptor(HTMLTextAreaElement.prototype, 'value').set.call(
          this,
          newValue,
        );
      },
    });
  }

  async onUserInput({ input, session, lastRoutePath }) {
    if (input.type === 'chatevent') return; // filter the typing event for the new version of @botonic/react}
    if (input.payload === DCEPayloads.StartNewChat) {
      setTimeout(this.updateBotonicTextInputReference.bind(this), 500);
    }
    if (this.livechat) {
      typingProviderInstance.vaTyping();

      await handleLivechatMessages(this, input);
    } else {
      this.onMessage && this.onMessage(this, { from: 'user', message: input });
      let resp = await this.bot.input({ input, session, lastRoutePath });

      this.onMessage && resp.response.map((r) => this.onMessage(this, { from: 'bot', message: r }));
      this.webchatRef.current.addBotResponse(resp);
    }
  }

  enableHeaderButtons({
    enableDownloadTranscriptButton = interactionIdExists(),
    enableEndConversationButton = true,
  } = {}) {
    const newTheme = this.theme.updateHeader({
      enableDownloadTranscriptButton,
      enableEndConversationButton,
    });

    this.updateWebchatSettings({
      theme: { ...newTheme },
    });
  }
}
