import React, { useRef, lazy, Suspense, useEffect } from 'react';
import type { FC } from 'react';
import { useForm, Controller, ValidateResult } from 'react-hook-form';
import { ThemeProps } from '@botonic/react';
import i18next from 'i18next';

import styles from './initialScreen.module.css';
import { pickBy, EMAIL_REGEX, PHONE_REGEX } from './utils/helper';

import './styles/common.module.css';
import './components/CustomField/CustomField.module.css';
import './components/CustomDropdown/CustomDropdown.module.css';
import './components/WelcomeMessage/WelcomeMessage.module.css';
import './components/CustomButton/CustomButton.module.css';
import './components/CustomMessage/CustomMessage.module.css';
import './components/CustomEmail/CustomEmail.module.css';
import './components/ErrorMessage/ErrorMessage.module.css';
import './components/CustomNumber/CustomNumber.module.css';

const CustomDropdown = lazy(() => import('./components/CustomDropdown'));
const WelcomeMessage = lazy(() => import('./components/WelcomeMessage'));
const CustomButton = lazy(() => import('./components/CustomButton'));
const CustomMessage = lazy(() => import('./components/CustomMessage'));
const CustomEmail = lazy(() => import('./components/CustomEmail'));
const CustomNumber = lazy(() => import('./components/CustomNumber'));
const CustomField = lazy(() => import('./components/CustomField'));

const customComponentMap = {
  dropdown: CustomDropdown,
  button: CustomButton,
  message: CustomMessage,
  field: CustomField,
};
const CustomFieldMap = {
  field_email: CustomEmail,
  field_number: CustomNumber,
  field_name: CustomField,
  field_option: CustomField,
};

const getComponentType = (controlItem) => {
  let Component;

  if (controlItem.define) {
    Component = CustomFieldMap[controlItem.define];
    if (!Component) {
      throw new Error(`Unknown field type: ${controlItem.define}`);
    }
  } else {
    Component = customComponentMap[controlItem.type];
    if (!Component) {
      throw new Error(`Unknown control type: ${controlItem.label}`);
    }
  }

  return Component;
};

const validateEmail = async (value: string): Promise<ValidateResult> => {
  const isValid = EMAIL_REGEX.test(value);

  if (!isValid && value) {
    return i18next.t('errorEmailAddressWarning') as string;
  }

  return true;
};

const validatePhoneNum = async (value: string): Promise<ValidateResult> => {
  const isValid = PHONE_REGEX.test(value);

  if (!isValid && value) {
    return i18next.t('errorPhoneNumberWarning') as string;
  }

  return true;
};

const validationMap = {
  field_email: validateEmail,
  field_number: validatePhoneNum,
};

interface Props {
  appId: string;
  closeComponent: () => void;
  initialScreenConfig: InitialScreenConfig;
  onSubmitInitialScreen: (
    customFields: Record<string, string>,
    enableStartConversation: boolean,
  ) => void;
  defaultValues: Record<string, string>;
  theme: ThemeProps;
  enableValidation: boolean;
}

interface InitialScreenConfig {
  initialScreen: InitialScreenProps;
  chat: Chat;
}

interface InitialScreenProps {
  showInitialScreen: boolean;
  welcomeMessage: IWelcomeMessage;
  controls: Array<Record<string, string>>;
}
interface Chat {
  messages: Array<string>;
}

interface IWelcomeMessage {
  title: string;
  subtitle: string;
}

const COLLECT_FIELDS = ['field', 'dropdown'];
const BUTTON_FIELD = ['button'];

export const InitialScreen: FC = (props: Props) => {
  const {
    initialScreenConfig: { initialScreen, chat } = {},
    onSubmitInitialScreen,
    theme,
    defaultValues,
    enableValidation,
  } = props;
  const initialScreenRef = useRef<HTMLDivElement | null>(null);
  const enableStartConversationRef = useRef<boolean>(false);

  /**
   * TODO: This should be temporary fix
   * Remove this block of code once Botonic releases a new version with this issue fixed https://github.com/hubtype/botonic/pull/2271
   * Scrolling does not work on the iPhone when the initial screen messages field is empty.
   * The code below will "trick" this part of the library code to force enable scrolling:
   * https://github.com/hubtype/botonic/blob/master/packages/botonic-react/src/webchat/devices/scrollbar-controller.js#L52
   */

  useEffect(() => {
    if (chat.messages.length === 0) {
      const el = document
        .getElementById('tdWebchat')
        .shadowRoot.querySelector('.simplebar-content');

      if (el && el.clientHeight === 0) {
        el.style.minHeight = '1px';
      }
    }
  }, [chat.messages.length]);

  const {
    handleSubmit,
    control,
    formState: { errors },
  } = useForm({
    mode: 'onChange',
    shouldFocusError: false,
  });

  const onSubmit = (formData) => {
    const collectFields = pickBy(formData, (_, val) => !!val);

    if (typeof onSubmitInitialScreen === 'function') {
      onSubmitInitialScreen(collectFields, enableStartConversationRef.current);
    }

    props.closeComponent();
  };

  return (
    <Suspense fallback={<></>}>
      <div className={styles.initialScreen} ref={initialScreenRef}>
        <div className={styles.wrapper}>
          <WelcomeMessage
            title={initialScreen?.welcomeMessage?.title}
            subTitle={initialScreen?.welcomeMessage?.subtitle}
          />

          <form>
            {initialScreen?.controls.map((controlItem) => {
              const Component = enableValidation
                ? getComponentType(controlItem)
                : customComponentMap[controlItem.type];

              return (
                <div key={controlItem.key} className={styles.formItem}>
                  {COLLECT_FIELDS.includes(controlItem.type) ? (
                    <Controller
                      name={controlItem?.argument}
                      control={control}
                      rules={{
                        required: controlItem?.required,

                        validate:
                          ['field_email', 'field_number'].includes(controlItem?.define) &&
                          enableValidation
                            ? validationMap[controlItem?.define]
                            : undefined,
                      }}
                      defaultValue={defaultValues?.[controlItem.argument]}
                      render={({ field }) => (
                        <Component
                          {...field}
                          label={controlItem.label}
                          options={controlItem.options || []}
                          required={controlItem.required}
                          isError={
                            errors[controlItem?.argument]?.type === 'required' ||
                            errors[controlItem?.argument]?.type === 'validate'
                          }
                        />
                      )}
                    />
                  ) : (
                    <>
                      {BUTTON_FIELD.includes(controlItem.type) ? (
                        <Component
                          theme={theme}
                          key={controlItem.key}
                          isBubble={controlItem.isBubble}
                          message={controlItem.value}
                          label={controlItem.label}
                          trigger={controlItem.trigger}
                          submitHandle={() => {
                            enableStartConversationRef.current =
                              !!controlItem?.enable_start_conversation;
                            handleSubmit(onSubmit)();
                          }}
                        />
                      ) : (
                        <Component
                          theme={theme}
                          key={controlItem.key}
                          isBubble={controlItem.isBubble}
                          message={controlItem.value}
                          label={controlItem.label}
                          trigger={controlItem.trigger}
                        />
                      )}
                    </>
                  )}
                </div>
              );
            })}
          </form>
        </div>
      </div>
    </Suspense>
  );
};
