/**
 * Creates an object composed of the object properties predicate returns truthy for
 * @param obj (Object) The source object.
 * @param predicate (function) the pickBy function
 * @return (Object) Returns the new object.
 */

type predicateType = (key: string, value: string, obj: Record<string, string>) => boolean;

export const pickBy = (obj: Record<string, string>, predicate: predicateType) => {
  if (obj == null) return obj;

  return Object.entries(obj).reduce((accumulator, [key, value]) => {
    if (predicate(key, value, obj)) {
      accumulator[key.toLowerCase()] = value;
    }

    return accumulator;
  }, {});
};

/**
 * Convert the incoming string to lowercase
 * @param str {String} The string to be converted
 */
export const toLowerCase = (str: string) => {
  if (typeof str === 'string') {
    return str.toLocaleLowerCase();
  }

  return '';
};

export const EMAIL_REGEX =
  /^(?!.{256})(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/i;

export const PHONE_REGEX = /^(\+\d{1,3}\s?)\(?\d{1,3}\)?[\s-]?\d{3,4}[\s-]?\d{3,5}/i;
